import React, { useState, useEffect } from 'react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

function App() {
    const [notes, setNotes] = useState([]);
    const [newNote, setNewNote] = useState('');
    const [usernameInput, setUsernameInput] = useState('');
    const [passwordInput, setPasswordInput] = useState('');
    const [authenticated, setAuthenticated] = useState(false);
    const [userInfo, setUserInfo] = useState({ username: '', roles: [] });

    //  
    const fetchNotes = async () => {
        try {
            const response = await fetch('/api/notes', { credentials: 'include' });
            if (response.ok) {
                const data = await response.json();
                setNotes(data);
            } else {
                console.error('  ');
            }
        } catch (error) {
            console.error('   :', error);
        }
    };

    //  
    const checkAuth = async () => {
        try {
            const response = await fetch('/api/auth/me', { credentials: 'include' });
            if (response.ok) {
                const user = await response.json();
                //   :      { authority: "..." }
                const roles = user.roles.map(role => role.authority);
                setAuthenticated(true);
                setUserInfo({ username: user.username, roles: roles });
                fetchNotes();
            } else {
                setAuthenticated(false);
            }
        } catch (error) {
            console.error('  :', error);
            setAuthenticated(false);
        }
    };

    useEffect(() => {
        checkAuth();
    }, []);

    // 
    const handleLogin = async (event) => {
        event.preventDefault();

        const formData = new URLSearchParams();
        formData.append('username', usernameInput);
        formData.append('password', passwordInput);

        try {
            const response = await fetch('/login', {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                body: formData,
                credentials: 'include'
            });

            if (response.ok) {
                await checkAuth();
                if (authenticated) {
                    toast.success(` , ${userInfo.username}!`);
                }
            } else {
                toast.error(' !');
                setAuthenticated(false);
            }
        } catch (error) {
            console.error(' :', error);
            toast.error('   !');
            setAuthenticated(false);
        }
    };

    // Logout
    const handleLogout = async () => {
        try {
            const response = await fetch('/logout', {
                method: 'POST',
                credentials: 'include'
            });

            if (response.ok) {
                setAuthenticated(false);
                setUserInfo({ username: '', roles: [] });
                setNotes([]);
                toast.success('    ');
            } else {
                console.error('  .');
            }
        } catch (error) {
            console.error(' logout:', error);
        }
    };

    //  
    const handleCreateNote = async (event) => {
        event.preventDefault();
        if (!newNote.trim()) return;

        try {
            const response = await fetch('/api/notes', {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ content: newNote }),
                credentials: 'include'
            });

            if (response.ok) {
                setNewNote('');
                fetchNotes();
            } else {
                console.error('  ');
            }
        } catch (error) {
            console.error('   :', error);
        }
    };

    //  
    const handleDeleteNote = async (id) => {
        if (!window.confirm(' ?')) return;

        try {
            const response = await fetch(`/api/notes/${id}`, {
                method: 'DELETE',
                credentials: 'include'
            });

            if (response.ok) {
                fetchNotes();
            } else {
                console.error('  ');
            }
        } catch (error) {
            console.error('   :', error);
        }
    };

    return (
    <div className="App">
    <h1> </h1>

    {authenticated ? (
    <>
    <div>
    <h3>: {userInfo.username}</h3>
<h4>: {userInfo.roles.join(', ')}</h4>
<button onClick={handleLogout}></button>
</div>

<form onSubmit={handleCreateNote}>
<h2> </h2>
<input
type="text"
placeholder="  "
value={newNote}
onChange={(e) => setNewNote(e.target.value)}
/>
<button type="submit"></button>
</form>

<h2> </h2>
<ul>
{notes.map((note) => (
<li key={note.id}>
<div dangerouslySetInnerHTML={{ __html: note.content }} />
<button onClick={() => handleDeleteNote(note.id)}></button>
</li>
))}
</ul>
</>
) : (
<form onSubmit={handleLogin}>
<h2></h2>
<input
type="text"
placeholder=" "
value={usernameInput}
onChange={(e) => setUsernameInput(e.target.value)}
autoComplete="username"
/>
<input
type="password"
placeholder=""
value={passwordInput}
onChange={(e) => setPasswordInput(e.target.value)}
autoComplete="current-password"
/>
<button type="submit"></button>
</form>
)}

<ToastContainer />
</div>
);
}

export default App;
